  margin-bottom: 1em;
}
body {
  background-color: #000;
}
.container {
  overflow: hidden;
}
.hero {
  background: rgba(29, 48, 90, 0.5);
  color: rgba(255, 255, 255, 0.85);
  padding: 2em;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  z-index: 10;
}
.hero[style*="position: fixed"] {
  font-size: 55%;
  left: 0 !important;
}
.title {
  font-size: 3em;
  margin: 0 0 0.2em;
}
.tagline {
