}
.svg-mountains {
  fill: #F0EFEB;
  position: absolute;
  top: -48px;
  left: 5vw;
  height: 50px;
  width: auto;
  z-index: 99;
}
.section {
  margin: 4em 0 0;
  padding: 0 10vw 0 0;
}
footer {
