.Wallop-buttonNext,
.Wallop-buttonPrevious {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  border: none;
  cursor: pointer;
  height: 40px;
  position: absolute;
  text-indent: -9999px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  width: 40px;
  z-index: 9;
}
.Wallop-buttonNext:hover,
  .Wallop-buttonPrevious:hover {
    background: rgba(255, 255, 255, 0.2);
}
.Wallop-buttonNext:hover:before,
    .Wallop-buttonPrevious:hover:before {
      color: rgba(255, 255, 255, 0.6);
}
.Wallop-buttonNext:before,
.Wallop-buttonPrevious:before {
  color: rgba(255, 255, 255, 0.4);
  font-size: 25px;
  left: 0;
  line-height: 0;
  position: absolute;
  text-align: center;
  text-indent: 0;
  top: 50%;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  width: 100%;
}
.Wallop-buttonNext:before {
  content: "\276F";
}
.Wallop-buttonPrevious:before {
  content: "\276E";
}
.Wallop-buttonPrevious {
  left: 1em;
}
.Wallop-buttonNext {
  right: 1em;
}
.slideshow-projects {
  border-radius: 3px;
  box-shadow: 0 0 0 2px grey, 0 0 0 15px #F0EFEB, 0 0 0 17px grey;
  margin: 2em 1em 3em;
  max-width: 700px;
}
.slideshow-projects .Wallop-list {
    background: #fff;
}



